import _ from 'underscore';

class ValueMap {
    constructor(value, name) {
        if (typeof value !== 'string') {
            throw new Error('ValueMapName: 非法的 value')
        }
        this._value = value;
        this._name = name;
        this[value] = name;
    }

    get value() {
        return this._value;
    }

    get name() {
        return this._name;
    }
    // 重写了 valueOf 方法, 在触发转化原始值时 返回的是 value
    // e.g: ValueMap a === 'this.value'  => true
    valueOf() {
        return this._value;
    }

    /**
     * get name by value map
     * @param { ValueMapName } mapping
     * @return { Object }
     */
    static getValueMap(mapping) {
        let valueMap = {};
        _.each(mapping, (item, key) => {
            valueMap[item.value] = item.name;
        });
        return valueMap;
    }
}

export default ValueMap;



