import q from 'q';
import QuRequest from '../core/QuRequest';
import QukanConfig from './QukanConfig';
import ValueMap from './ValueMap';
const quRequest = new QuRequest();

class ArticlePush {
    constructor() {
        this._areaTypes = {
            'ALL': new ValueMap('0', '全部'),
            'DENY_AREA': new ValueMap('1', '屏蔽地区'),
            'TARGET_AREA': new ValueMap('2', '指定地区')
        };

        this._pushPlatform = {
            'ALL': new ValueMap('0', '全部推送'),
            'ANDROID': new ValueMap('1', '安卓'),
            'IOS': new ValueMap('2', 'IOS'),
        };

        this._pushWays = {
            'WAY_IMMEDIATELY': new ValueMap('1', '广播立即推送'),
            'WAY_TIMING': new ValueMap('2', '广播定时推送'),
            'WAY_PERSONALITY': new ValueMap('3', '个性化定时推送')
        };

        this._pushStatus = {
            'STATUS_CONFIRMED': new ValueMap('1', '待确认'),
            'STATUS_STARED': new ValueMap('2', '未开始'),
            'STATUS_NORMAL': new ValueMap('3', '正常'),
            'STATUS_WITHDRAWN': new ValueMap('4', '已撤回'),
            'STATUS_CANCELED': new ValueMap('5', '已取消')
        };

        this._delayStatus = {
            'DELAY_YES': new ValueMap('1', '延迟推送'),
            'DELAY_NO': new ValueMap('2', '非延迟推送')
        };

        this._targetAreas = ["北京市","上海市","天津市","江苏省","重庆市","河北省","安徽省","浙江省","广东省","福建省","山西省","辽宁省","吉林省","黑龙江省","山东省","河南省","湖北省","湖南省","海南省","四川省","贵州省","云南省","陕西省","甘肃省","青海省","江西省","西藏自治区","广西壮族自治区","内蒙古自治区","宁夏回族自治区","新疆维吾尔自治区","香港","澳门","台湾"];

        this._getDenyAreasPromise = null;
    }

    get areaTypes() {
        return this._areaTypes;
    }

    get pushPlatform() {
        return this._pushPlatform;
    }

    get pushWays() {
        return this._pushWays;
    }

    get pushStatus() {
        return this._pushStatus;
    }

    get delayStatus() {
        return this._delayStatus;
    }

    get targetAreas() {
        return this._targetAreas;
    }

    getDenyAreas() {
        if (this._getDenyAreasPromise) {
            return this._getDenyAreasPromise;
        }

        this._getDenyAreasPromise = QukanConfig.getConfig('blackPushCity')
            .then(data => {
                let city = [];
                try {
                    city = JSON.parse(data.value).city;
                } catch (error) {}
                return city || [];
            })
            .catch(err => {
                this._getDenyAreasPromise = null;
                throw err;
            });

        return this._getDenyAreasPromise;
    }

    getList(reqData) {
        return quRequest.send(quRequest.Apis.qukan.pushContent.getPushList, reqData)
            .then(({ code, data, messge }) => {
                return {
                    list: data.data,
                    totalPage: data.total_page
                }
            })
            .catch(err => {
                throw new Error(`获取推送列表数据失败: ${ err.message }`);
            });
    }

    getLogList(reqData) {
        return quRequest.send(quRequest.Apis.qukan.pushContent.getLogList, reqData)
            .then(({ code, data, messge }) => {
                return {
                    list: data.data,
                    totalPage: data.total_page
                }
            });
    }

    cancelPushContent(reqData) {
        return quRequest.send(quRequest.Apis.qukan.pushContent.cancelPushContent, reqData);
    }

    confirmPushContent(reqData) {
        return quRequest.send(quRequest.Apis.qukan.pushContent.confirmPush, reqData);
    }

    pushContent(reqData) {
        return quRequest.send(quRequest.Apis.qukan.pushContent.pushContent, reqData);
    }

    recallPush(reqData) {
        return quRequest.send(quRequest.Apis.qukan.pushContent.recallPush, reqData);
    }

    modify(reqData) {
        return quRequest.send(quRequest.Apis.qukan.pushContent.modify, reqData);
    }

    getPushDetail(reqData) {
        return quRequest.send(quRequest.Apis.qukan.pushContent.getPushDetail, reqData)
            .then(({ code, data, message }) => {
                return data;
            });
    }

    getPushDocument(reqData) {
        return quRequest.send(quRequest.Apis.qukan.pushContent.getPushDocument, reqData)
            .then(({ code, data, message }) => {
                return data;
            });
    }

}

export default ArticlePush;



