import QuRequest from '../core/QuRequest';

const quRequest = new QuRequest();

class QukanConfig {
    constructor() {}

    static getConfig(name, platform = 1) {
        return quRequest.send(quRequest.Apis.qukan.config.getConfigList, {
            platform,
            name
        }).then(({ code, message, data }) => {
            if (data.length) {
                data = data[0][platform];
                return data;
            } else {
                throw new Error("配置不存在");
            }
        });
    }
}

export  default QukanConfig;



