<template>
    <div>
        <div class="ibox">
            <div class="ibox-title">
                <div class="pull-right control">
                    <!--<a href="http://106.75.78.152/#/workbooks/130/views" target="_blank">-->
                        <!--<el-button size="small" type="default">查看推送数据</el-button>-->
                    <!--</a>-->
                    <el-button size="small" @click="$router.push({ name: 'ArticlePushEdit' })" type="info" icon="edit">添加推送 </el-button>
                </div>
                <h3> 推送记录 </h3>
            </div>
            <div class="ibox-filter">
                <el-form :inline="true" label-position="top">
                    <el-form-item label="状态">
                        <el-select
                            size="small"
                            @change="filterAction()"
                            v-model="filter.status" >
                            <el-option label="全部" value=""></el-option>
                            <el-option
                                v-for="(item, $key) in pushStatus"
                                :label="item.name"
                                :value="item.value"
                                :key="$key">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="推送日期范围">
                        <el-date-picker
                            v-model="dateTimeRange"
                            @change="filterAction()"
                            type="daterange"
                            size="small"
                            placeholder="选择日期范围"
                            align="right">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="快捷日期">
                        <el-tag @click.native="handleQuickRange($event, 0)" type="info">今天</el-tag>
                        <el-tag @click.native="handleQuickRange($event, 15)" type="info">15天</el-tag>
                        <el-tag @click.native="handleQuickRange($event, 30)" type="info">30天</el-tag>
                    </el-form-item>
                </el-form>
            </div>
            <div class="ibox-filter">
                <el-form :inline="true" label-position="top">
                    <el-form-item label="推送标题">
                        <el-input placeholder="推送标题"
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model.trim="filter.push_title">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="推送描述">
                        <el-input placeholder="推送描述"
                                  size="small"
                                  @keyup.enter.native="filterAction()"
                                  v-model.trim="filter.push_message">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="文章ID">
                        <el-input placeholder="文章ID"
                                  size="small"
                                  type="number"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.content_id">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="推送人ID">
                        <el-input placeholder="推送人ID"
                                  size="small"
                                  type="number"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.op_id">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="审核人ID">
                        <el-input placeholder="审核人ID"
                                  size="small"
                                  type="number"
                                  @keyup.enter.native="filterAction()"
                                  v-model="filter.review_op_id">
                        </el-input>
                    </el-form-item>
                    <el-form-item label="操作">
                        <el-button @click="filterAction($event)" type="primary" icon="search">搜索</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="ibox-content">
                <div class="control">
                    <el-table :data="filterListDate"
                              :row-class-name="tabRowClass"
                              element-loading-text="拼命加载中"
                              v-loading="listData.loading"
                              style="width: 100%">
                        <el-table-column label="推送时间"
                                         width="200"
                                         prop="publish_time">
                        </el-table-column>
                        <el-table-column label="文章ID"
                                         width="120px">
                            <template scope="scope">
                                <a :href="scope.row.url" target="_blank">{{ scope.row.content_id }}</a>
                            </template>
                        </el-table-column>
                        <el-table-column label="推送标题"
                                         min-width="200"
                                         prop="push_title">
                        </el-table-column>
                        <el-table-column label="推送描述"
                                         min-width="230"
                                         prop="push_message">
                        </el-table-column>
                        <el-table-column label="阅读数 / 当前"
                                         width="130">
                            <template scope="scope">{{ scope.row.read_count + ' / ' + scope.row.now_read_count }}</template>
                        </el-table-column>
                        <el-table-column label="评论数 / 当前"
                                         width="130"
                                         prop="now_comment_count">
                            <template scope="scope">{{ scope.row.comment_count + ' / ' + scope.row.now_comment_count }}</template>
                        </el-table-column>
                        <el-table-column label="推送方式"
                                         width="140"
                                         prop="push_way_name">
                        </el-table-column>
                        <el-table-column label="状态"
                                         width="80"
                                         prop="push_status_name">
                        </el-table-column>
                        <el-table-column label="操作"
                                         width="220">
                            <template scope="scope">
                                <div v-show="!scope.row.is_sum">
                                    <el-button v-if="scope.row.status === pushStatus.STATUS_CONFIRMED.value" size="small" type="primary" @click="handleConfirm($event, scope.$index, scope.row)">确认</el-button>
                                    <el-button size="small" type="info" @click="handleDetail($event, scope.$index, scope.row)">详情</el-button>
                                    <el-dropdown>
                                        <el-button size="small">
                                            更多<i class="el-icon-caret-bottom el-icon--right"></i>
                                        </el-button>
                                        <el-dropdown-menu slot="dropdown">
                                            <el-dropdown-item
                                                v-if="[pushStatus.STATUS_CONFIRMED.value, pushStatus.STATUS_STARED.value, pushStatus.STATUS_NORMAL.value].indexOf(scope.row.status) != -1"
                                                @click.native="handleCancel($event, scope.$index, scope.row)">
                                                <span>取消</span>
                                            </el-dropdown-item>
                                            <el-dropdown-item
                                                v-if="[pushStatus.STATUS_NORMAL.value].indexOf(scope.row.status) != -1"
                                                @click.native="handleRecall($event, scope.$index, scope.row)">
                                                <span>撤回</span>
                                            </el-dropdown-item>
                                            <el-dropdown-item @click.native="handleLog($event, scope.$index, scope.row)">
                                                <span>操作记录</span>
                                            </el-dropdown-item>
                                            <el-dropdown-item v-show="scope.row.status === pushStatus.STATUS_CONFIRMED.value" @click.native="$router.push({name: 'ArticlePushEdit', query: { id: scope.row.id }})">
                                                <span>修改</span>
                                            </el-dropdown-item>
                                        </el-dropdown-menu>
                                    </el-dropdown>
                                </div>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>

                <div class="control">
                    <el-pagination @current-change="handleCurrentChange"
                                   :current-page="filter.page"
                                   :page-size="20"
                                   :page-count="listData.totalPage"
                                   layout="total, prev, pager, next, jumper">
                    </el-pagination>
                </div>
            </div>
        </div>

        <el-dialog title="详情"
                   :visible.sync="dialogDetail.visible" >
            <el-form
                v-loading="dialogDetail.loading"
                element-loading-text="拼命加载中">
                <el-row>
                    <el-col :lg="12">
                        <el-form-item label="推送人:" label-width="100px">
                            {{ dialogDetailData.op_name }}
                        </el-form-item>
                    </el-col>
                    <el-col :lg="12">
                        <el-form-item label="确认人:" label-width="100px">
                            {{ dialogDetailData.review_op_name }}
                        </el-form-item>
                    </el-col>
                    <el-col :lg="12">
                        <el-form-item label="推送方式:" label-width="100px">
                            {{ dialogDetailData.push_way_name }}
                        </el-form-item>
                    </el-col>
                    <el-col :lg="12">
                        <el-form-item label="推送时间:" label-width="100px">
                            {{ dialogDetailData.publish_time }}
                        </el-form-item>
                    </el-col>
                    <el-col :lg="24">
                        <el-form-item label="推送标题:" label-width="100px">
                            {{ dialogDetailData.push_title }}
                        </el-form-item>
                    </el-col>
                    <el-col :lg="24">
                        <el-form-item label="推送描述:" label-width="100px">
                            {{ dialogDetailData.push_message }}
                        </el-form-item>
                    </el-col>
                    <el-col :lg="12">
                        <el-form-item label="推送平台:" label-width="100px">
                            {{ dialogDetailData.platform_name }}
                        </el-form-item>
                    </el-col>
                    <el-col :lg="12">
                        <el-form-item :label="dialogDetailData.push_area_type_name + ':'" label-width="100px">
                            {{ dialogDetailData.push_area }}
                        </el-form-item>
                    </el-col>
                    <el-col :lg="24">
                        <el-form-item label="文章:" label-width="100px">
                            [ id: {{ dialogDetailData.content_id }} ]&nbsp;&nbsp;&nbsp;<a :href="dialogDetailData.url" target="_blank">{{ dialogDetailData.content_title }}</a>
                        </el-form-item>
                    </el-col>
                </el-row>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogDetail.visible = false">关闭</el-button>
            </div>
        </el-dialog>


        <el-dialog title="操作日志"
                   :visible.sync="dialogLog.visible" >
            <el-table
                :data="dialogLog.data"
                element-loading-text="拼命加载中"
                v-loading="dialogLog.loading"
                style="width: 100%">
                <el-table-column
                    prop="create_time"
                    label="时间">
                </el-table-column>
                <el-table-column
                    prop="op_name"
                    label="操作人">
                </el-table-column>
                <el-table-column
                    prop="action"
                    label="所做操作">
                </el-table-column>
            </el-table>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogLog.visible = false">关闭</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<style scope="" lang="less" type="text/scss">
.el-table {
    tr:hover {
        td {
            background-color: initial !important;
        }
    }
    tr.group_item {
        background: rgba(0, 0 ,0 , 0.05);
    }
    tr.group_foot {
        background: #18bda5;
        color: #fff;
    }
}
</style>

<script>
import q from 'q';
import moment from 'moment';
import ArticlePushModel from '../../module/ArticlePush';
import toolMixin from '../../mixin/toolMixin';
import ValueMap from '../../module/ValueMap';

let articlePushModel = null;

export default {
//    mixins: [ listMixin, toolMixin ],
    components: {
    },
    beforeCreate() {
        articlePushModel = new ArticlePushModel();
    },
    created() {
        let { start_date, end_date } = this.filter;
        if (start_date && end_date) {
            this.dateTimeRange = [
                moment(start_date).toDate(),
                moment(end_date).toDate()
            ];
        }
    },
    mounted() {
        this.getList();
    },
    data() {
        return {
            dateTimeRange: [],
            listData: {
                list: [],
                totalPage: 1,
                loading: false
            },
            filter: {
                "status": "",
                "content_id": "",
                "start_date": '',
                "end_date": '',
                "push_title": "",
                "push_message": "",
                "op_id": "",
                "review_op_id": "",
                "page": 1,
                "page_size": 30
            },
            dialogDetail: {
                visible: false,
                loading: false,
                data: {}
            },
            dialogLog: {
                visible: false,
                loading: false,
                data: []
            }
        }
    },
    watch: {
        dateTimeRange(nv) {
            let [ start, end ] = nv || [];
            this.filter.start_date = start ? moment(start).format('YYYY-MM-DD') : '';
            this.filter.end_date = end ? moment(end).format('YYYY-MM-DD') : '';
        }
    },
    computed: {
        filterListDate() {
            let pushWaysValueMap = ValueMap.getValueMap(articlePushModel.pushWays);
            let pushStatusValueMap = ValueMap.getValueMap(articlePushModel.pushStatus);
            let list = this.listData.list;

            let stashData = [];
            let rst = []
            list.map((item, $index) => {
                let prevItem = list[$index - 1] || {};
                let nextItem = list[$index + 1] || {};
                let prevPublishTime = prevItem.publish_time;
                let nextPublishTime = nextItem.publish_time;
                let WAY_PERSONALITY = articlePushModel.pushWays.WAY_PERSONALITY.value;
                // 个性化推送按组显示 ( head, foot 内阴影 )
                let groupType = '';
                switch(true) {
                    case item.push_way !== WAY_PERSONALITY:
                    break;
                    case !prevPublishTime && item.publish_time !== nextPublishTime:
                    case !nextPublishTime && item.publish_time !== prevPublishTime:
                    break;
                    case (item.publish_time !== prevPublishTime || prevItem.push_way !== WAY_PERSONALITY) && item.publish_time === nextPublishTime:
                        stashData = [];
                        groupType = 'head';
                    break;
                    case (item.publish_time !== nextPublishTime || nextItem.push_way !== WAY_PERSONALITY) && item.publish_time === prevPublishTime:
                        groupType = 'foot';
                    break;
                }

                stashData.push(item);
                item.push_way_name = pushWaysValueMap[item.push_way];
                item.push_status_name = pushStatusValueMap[item.status];
                rst.push(item);

                if (groupType === 'foot') {
                    stashData = stashData.map(data => {
                        data.group_class = 'group_item';
                        return data;
                    })
                    let sumItem = this.rowSum(stashData);
                    rst.push(sumItem);
                }
            });
            return rst;
        },
        dialogDetailData() {
            let data = this.dialogDetail.data;
            let { areaTypes, pushWays, pushPlatform } = articlePushModel;
            let pushWayValueMap = ValueMap.getValueMap(pushWays);
            let pushPlatformMap = ValueMap.getValueMap(pushPlatform);

            return {
                op_name: data.op_id,
                review_op_name: data.review_op_id,
                push_way_name: pushWayValueMap[data.push_way],
                publish_time: data.publish_time,
                push_title: data.push_title,
                push_message: data.push_message,
                platform_name: pushPlatformMap[data.platform],
                push_area_type_name: data.is_shielding == '1' ?  areaTypes.DENY_AREA.name : areaTypes.TARGET_AREA.name,
                push_area: data.push_regional ? data.push_regional.join(',') : '',
                content_id: data.content_id,
                content_title: data.content_title,
                url: data.url
            };
        },
        pushStatus() { return articlePushModel.pushStatus; },
        areaTypes() { return articlePushModel.areaTypes; },
        pushPlatform() { return articlePushModel.pushPlatform; },
        pushWays() { return articlePushModel.pushWays; },
    },
    methods: {
        rowSum(data) {
            let sumData = {
                publish_time: '合计',
                push_title: '-',
                push_message: '-',
                read_count: 0,
                comment_count: 0,
                now_read_count: 0,
                now_comment_count: 0,
                push_way_name: '-',
                push_status_name: '-',
                content_id: '-',
                url: '',
                is_sum: true,
                group_class: 'group_foot'
            };
            data.forEach(item => {
                sumData.now_read_count += parseInt(item.now_read_count || 0);
                sumData.now_comment_count += parseInt(item.now_comment_count || 0);
                sumData.read_count += parseInt(item.read_count || 0);
                sumData.comment_count += parseInt(item.comment_count || 0);
            });
            return sumData;
        },
        tabRowClass(row, $index) {
            return row.group_class;
        },
        filterAction() {
            this.filter.page = 1;
            this.getList();
        },
        handleCurrentChange(page) {
            this.filter.page = page;
            this.getList();
        },
        handleQuickRange($event, day) {
            this.dateTimeRange = [ moment().subtract(day, 'day').toDate(), moment().add('1', 'day').toDate() ];
        },
        handleLog($event, $index, item) {
            if (this.dialogLog.visible) {
                return;
            }
            this.dialogLog.data = [];
            this.dialogLog.visible = true;
            this.dialogLog.loading = true;

            articlePushModel.getLogList({
                push_id: item.id
            }).then(data => {
                this.dialogLog.data = data.list;
            }).catch(error => {
                this.$message.error(`获取日志失败: ${ error.message }`);
                throw error;
            }).fin(() => {
                this.dialogLog.loading = false;
            }).done();
        },
        handleDetail($event, $index, item) {
            if (this.dialogDetail.visible) {
                return;
            }
            this.dialogDetail.data = {};
            this.dialogDetail.visible = true;
            this.dialogDetail.loading = true;

            articlePushModel.getPushDetail({
                push_id: item.id
            }).then(data => {
                this.dialogDetail.data = data;
            }).catch(error => {
                this.$message.error(`获取详情数据失败: ${ error.message }`);
                throw error;
            }).fin(() => {
                this.dialogDetail.loading = false;
            }).done();
        },
        handleConfirm($event, $index, item) {
            let $elem = $event.target;
            let { $blockRequest, $message, listData } = this;

            return q.fcall(() => {
                return this.$confirm('确定后，系统将按照设定时间推送文章！', '是否确定推送', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                });
            }).then(() => {
                return $blockRequest({
                    $elem: $elem,
                    req: articlePushModel.confirmPushContent({
                        push_id: item.id
                    }),
                    pendingText: '确认中'
                });
            }, () => {
                throw new Error('取消确认');
            }).then(({ data: { status } }) => {
                $message.success(`确认 ${item.push_title.substring(0, 10)}... 成功`);
                item.status = status;
            }).catch(this.errorHandle)
                .done();
        },
        handleRecall($event, $index, item) {
            let $elem = $event.target;
            let { $blockRequest, $message, listData } = this;

            return q.fcall(() => {
                return this.$confirm('确定后，系统将撤回推送文章！', '是否确定撤回', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                });
            }).then(() => {
                return $blockRequest({
                    $elem: $elem,
                    req: articlePushModel.recallPush({
                        push_id: item.id
                    }),
                    pendingText: '撤回中'
                });
            }, () => {
                throw new Error('已取消撤回');
            }).then(res => {
                $message.success(`撤回 ${item.push_title.substring(0, 10)}... 成功`);
                item.status = articlePushModel.pushStatus.STATUS_WITHDRAWN.value;
            }).catch(this.errorHandle)
            .done();
        },
        handleCancel($event, $index, item) {
            let $elem = $event.target;
            let { $blockRequest, $message, listData } = this;

            return q.fcall(() => {
                return this.$confirm('确定后，系统将取消推送文章！', '是否确定取消', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                });
            }).then(() => {
                return $blockRequest({
                    $elem: $elem,
                    req: articlePushModel.cancelPushContent({
                        push_id: item.id
                    }),
                    pendingText: '取消中'
                });
            }, () => {
                throw new Error('已取消');
            }).then(res => {
                $message.success(`取消 ${item.push_title.substring(0, 10)}... 成功`);
                item.status = articlePushModel.pushStatus.STATUS_CANCELED.value;
            }).catch(this.errorHandle)
                .done();
        },
        getList() {
            let { filter, $message } = this;
            this.$updateRouter(filter);
            this.listData.loading = true;
            return articlePushModel
                .getList(filter)
                .then(({ list, totalPage }) => {
                    this.listData.list = list;
                    this.listData.totalPage = totalPage;
                }).catch(reason => {
                    $message.error(`获取列表数据失败：${reason.message || '未知原因'} `);
                    throw reason;
                }).fin(() => {
                    this.listData.loading = false;
                }).done();
        }
    }
}

</script>



